        <div
          :class="{'hazardtype': true, 'hazardtypewarning': item.noConfig }"
          @click="floodindex =floodindex === index ? -1 : index"
        >{{item.type}}</div>
        <div class="hazardmapping" v-show="floodindex === index">
          <span v-for="(itemconfig, configindex) in item.mapping" :key="configindex">
            <span class="hazardmappingrow">
              <input :value="item.type" readonly disabled>
              <input :value="itemconfig.layerKey" readonly disabled>
              <input :value="itemconfig.rangeFrom" readonly disabled>
              <input :value="itemconfig.rangeTo" readonly disabled>
              <div class="delete" @click="removeLayerConfig(itemconfig.layerKey,'flood')">Del.</div>
            </span>
          </span>
          <span class="hazardmappingrow" style="border-top: 1px dashed">
            <input :value="item.type" readonly disabled>
            <input placeholder="Layer Key" v-model="savedDataFlood.layerKey">
            <input
              :placeholder="'Range From ('+item.minValue+')'"
              v-model="savedDataFlood.rangeFrom"
            >
            <input
              :placeholder="'Range To ('+item.maxValue+')'"
              v-model="savedDataFlood.rangeTo"
              @keyup.enter="savedDataFlood.type = item.type ,saveHazardConfig('flood')"
            >
            <div
              class="save"
              @click="savedDataFlood.type = item.type ,saveHazardConfig('flood')"
            >Save</div>
          </span>
        </div>
      </li>
    </ul>
    <p>Climatological</p>
    <ul>
      <li v-for="(item, index) in climatological" :key="index">
        <div
          :class="{'hazardtype': true, 'hazardtypewarning': item.noConfig }"
          @click="climatologicalindex =climatologicalindex === index ? -1 : index"
        >{{item.type}}</div>
        <div class="hazardmapping" v-show="climatologicalindex === index">
          <span v-for="(itemconfig, configindex) in item.mapping" :key="configindex">
            <span class="hazardmappingrow">
              <input :value="item.type" readonly disabled>
              <input :value="itemconfig.layerKey" readonly disabled>
              <input :value="itemconfig.rangeFrom" readonly disabled>
