    </ul>
    <p>Others</p>
    <ul>
      <li v-for="(item, index) in others" :key="index">
        <div
          :class="{'hazardtype': true, 'hazardtypewarning': item.noConfig }"
          @click="othersindex =othersindex === index ? -1 : index"
        >{{item.type}}</div>
        <div class="hazardmapping" v-show="othersindex === index">
          <span class="hazardmappingrow">
            <input value="Hazard Type" readonly disabled style="font-weight: bold">
            <input value="Hazard Category" readonly disabled style="font-weight: bold">
            <input value="Source" readonly disabled style="font-weight: bold">
            <input value="Layer Key[UI]" readonly disabled style="font-weight: bold">
            <input value="Range From" readonly disabled style="font-weight: bold">
            <input value="Range To" readonly disabled style="font-weight: bold">
            <div class="delete" style="border: none"></div>
          </span>
          <span v-for="(itemconfig, configindex) in item.mapping" :key="configindex">
            <span class="hazardmappingrow">
              <input :value="item.type" readonly disabled>
              <input :value="itemconfig.hazardCategory" readonly disabled>
              <input :value="itemconfig.source" readonly disabled>
              <input :value="itemconfig.layerKey" readonly disabled>
              <input :value="itemconfig.rangeFrom" readonly disabled>
              <input :value="itemconfig.rangeTo" readonly disabled>
              <div class="delete" @click="removeLayerConfig(itemconfig.layerKey,'others')">Del.</div>
            </span>
          </span>
          <span class="hazardmappingrow" style="border-top: 1px dashed">
            <input :value="item.type" readonly disabled>
            <input placeholder="Layer Key" v-model="savedDataOthers.layerKey">
            <input
              :placeholder="'Range From ('+item.minValue+')'"
              v-model="savedDataOthers.rangeFrom"
            >
            <input
              :placeholder="'Range To ('+item.maxValue+')'"
              v-model="savedDataOthers.rangeTo"
              @keyup.enter="savedDataOthers.type = item.type ,saveHazardConfig('others')"
            >
            <div
              class="save"
