</template>

<script>
import https from "@/https";
import { EventBus } from "@/event-bus.js";

export default {
  name: "HazardLayer",
  component: {},

  data() {
    return {
      deleteItem: undefined,
      deleteItemFrom: undefined,
      earthquakeindex: -1,
      stormindex: -1,
      floodindex: -1,
      climatologicalindex: -1,
      othersindex: -1,
      earthquake: [],
      storm: [],
      flood: [],
      climatological: [],
      others: [],
      savedDataEarthquake: {
        type: "",
        layerKey: "",
        rangeFrom: "",
        rangeTo: ""
      },
      savedDataStorm: { type: "", layerKey: "", rangeFrom: "", rangeTo: "" },
      savedDataFlood: { type: "", layerKey: "", rangeFrom: "", rangeTo: "" },
      savedDataClimatological: {
        type: "",
        layerKey: "",
        rangeFrom: "",
        rangeTo: ""
      },
      savedDataOthers: { type: "", layerKey: "", rangeFrom: "", rangeTo: "" }
    };
  },
  methods: {
    removeLayerConfig: function(key, from) {
      if (key !== null) {
        this.deleteItem = key;
        this.deleteItemFrom = from;
        EventBus.$emit("Popup", {
          popupType: "Confirmation",
          title: "Hazard Layer Deletion",
          message: `Are you sure you want to delete this[${key}] item?`,
          confirmFunction: this.deleteConfirmed
        });
      }
    },
    deleteConfirmed: function() {
      https
        .delete(
          `/api/configuration/hazardconfigurations.xsjs?key=${this.deleteItem}`
        )
        .then(response => {
          if (response.data === 1) {
            this.deleteItemFrom === "earthquake"
              ? this.loadEarthquake()
              : this.deleteItemFrom === "storm"
              ? this.loadStorm()
              : this.deleteItemFrom === "flood"
              ? this.loadFlood()
              : this.deleteItemFrom === "climatological"
              ? this.loadClimatological()
              : this.deleteItemFrom === "others"
              ? this.loadOthers()
              : "";

            this.deleteItem = undefined;
            this.deleteItemFrom = undefined;
          } else alert("Could not process Delete..");
        })
        .catch(e => alert(JSON.stringify(e.response.data)));
    },
    loadEarthquake: async function() {
      await https
        .get("/api/configuration/hazardconfigurations.xsjs?type=EARTHQUAKE")
        .then(response => {
          this.earthquake = response.data;
        })
        .catch(e => alert(JSON.stringify(e.response.data)));
    },
    loadStorm: async function() {
      await https
        .get("/api/configuration/hazardconfigurations.xsjs?type=STORM")
        .then(response => {
          this.storm = response.data;
        })
        .catch(e => alert(JSON.stringify(e.response.data)));
    },
    loadFlood: async function() {
      await https
        .get("/api/configuration/hazardconfigurations.xsjs?type=FLOOD")
        .then(response => {
          this.flood = response.data;
        })
        .catch(e => alert(JSON.stringify(e.response.data)));
    },
    loadClimatological: async function() {
      await https
        .get("/api/configuration/hazardconfigurations.xsjs?type=CLIMATOLOGICAL")
        .then(response => {
          this.climatological = response.data;
        })
        .catch(e => alert(JSON.stringify(e.response.data)));
    },
    loadOthers: async function() {
      await https
        .get("/api/configuration/hazardconfigurations.xsjs?type=OTHERS")
        .then(response => {
          this.others = response.data;
        })
        .catch(e => alert(JSON.stringify(e.response.data)));
    },
    saveHazardConfig: function(type) {
      if (type === "earthquake") {
        if (
          this.savedDataEarthquake.rangeFrom === null ||
          this.savedDataEarthquake.rangeFrom === undefined ||
          this.savedDataEarthquake.rangeFrom === "" ||
          this.savedDataEarthquake.rangeTo === null ||
          this.savedDataEarthquake.rangeTo === undefined ||
          this.savedDataEarthquake.rangeTo === ""
        ) {
          alert("Range Cannot be Empty");
        } else if (
          isNaN(this.savedDataEarthquake.rangeFrom) ||
          isNaN(this.savedDataEarthquake.rangeTo)
        ) {
          alert("Ranges should be Number");
        } else if (
          this.savedDataEarthquake.layerKey === null ||
          this.savedDataEarthquake.layerKey === undefined ||
          this.savedDataEarthquake.layerKey === ""
        ) {
          alert("Layer Key cannot be Empty");
        } else {
          https
            .post(
              "/api/configuration/hazardconfigurations.xsjs",
              JSON.stringify(this.savedDataEarthquake)
            )
            .then(
              response => {
                if (response.data === 1) {
                  this.savedDataEarthquake = {
                    type: "",
                    layerKey: "",
                    rangeFrom: "",
                    rangeTo: ""
                  };
                  this.loadEarthquake();
                } else {
                  EventBus.$emit("Popup", {
                    popupType: "Error",
                    title: "Error Saving Hazard Values..!",
                    message: "Could not save Hazard Values. Try Again"
                  });
                }
              },
              error => {
                EventBus.$emit("Popup", {
                  popupType: "Error",
                  title: "Error Saving Hazard Values..!",
                  message: error.response.data.includes(
                    "unique constraint violated"
                  )
                    ? "301 - unique constraint violated"
                    : error
                });
              }
            );
        }
      }
      if (type === "storm") {
        if (
          this.savedDataStorm.rangeFrom === null ||
          this.savedDataStorm.rangeFrom === undefined ||
          this.savedDataStorm.rangeFrom === "" ||
          this.savedDataStorm.rangeTo === null ||
          this.savedDataStorm.rangeTo === undefined ||
          this.savedDataStorm.rangeTo === ""
        ) {
          alert("Range Cannot be Empty");
        } else if (
          isNaN(this.savedDataStorm.rangeFrom) ||
          isNaN(this.savedDataStorm.rangeTo)
        ) {
          alert("Ranges should be Number");
        } else if (
          this.savedDataStorm.layerKey === null ||
          this.savedDataStorm.layerKey === undefined ||
          this.savedDataStorm.layerKey === ""
        ) {
          alert("Layer Key cannot be Empty");
        } else {
          https
            .post(
              "/api/configuration/hazardconfigurations.xsjs",
              JSON.stringify(this.savedDataStorm)
            )
            .then(
              response => {
                response.data !== 1 ? alert("Error Occured") : "";
                this.savedDataStorm = {
                  type: "",
                  layerKey: "",
                  rangeFrom: "",
                  rangeTo: ""
                };
                this.loadStorm();
              },
              error => {
                EventBus.$emit("Popup", {
                  popupType: "Error",
                  title: "Error Saving Hazard Values..!",
                  message: error.response.data.includes(
                    "unique constraint violated"
                  )
                    ? "301 - unique constraint violated"
                    : error
                });
              }
            );
        }
      }
      if (type === "flood") {
        if (
          this.savedDataFlood.rangeFrom === null ||
          this.savedDataFlood.rangeFrom === undefined ||
          this.savedDataFlood.rangeFrom === "" ||
          this.savedDataFlood.rangeTo === null ||
          this.savedDataFlood.rangeTo === undefined ||
          this.savedDataFlood.rangeTo === ""
        ) {
          alert("Range Cannot be Empty");
        } else if (
          isNaN(this.savedDataFlood.rangeFrom) ||
          isNaN(this.savedDataFlood.rangeTo)
        ) {
          alert("Ranges should be Number");
        } else if (
          this.savedDataFlood.layerKey === null ||
          this.savedDataFlood.layerKey === undefined ||
          this.savedDataFlood.layerKey === ""
        ) {
          alert("Layer Key cannot be Empty");
        } else {
          https
            .post(
              "/api/configuration/hazardconfigurations.xsjs",
              JSON.stringify(this.savedDataFlood)
            )
            .then(
              response => {
                response.data !== 1 ? alert("Error Occured") : "";
                this.savedDataFlood = {
                  type: "",
                  layerKey: "",
                  rangeFrom: "",
                  rangeTo: ""
                };
                this.loadFlood();
              },
              error => {
                EventBus.$emit("Popup", {
                  popupType: "Error",
                  title: "Error Saving Hazard Values..!",
                  message: error.response.data.includes(
                    "unique constraint violated"
                  )
                    ? "301 - unique constraint violated"
                    : error
                });
              }
            );
        }
      }
      if (type === "climatological") {
        if (
          this.savedDataClimatological.rangeFrom === null ||
          this.savedDataClimatological.rangeFrom === undefined ||
          this.savedDataClimatological.rangeFrom === "" ||
          this.savedDataClimatological.rangeTo === null ||
          this.savedDataClimatological.rangeTo === undefined ||
          this.savedDataClimatological.rangeTo === ""
        ) {
          alert("Range Cannot be Empty");
        } else if (
          isNaN(this.savedDataClimatological.rangeFrom) ||
          isNaN(this.savedDataClimatological.rangeTo)
        ) {
          alert("Ranges should be Number");
        } else if (
          this.savedDataClimatological.layerKey === null ||
          this.savedDataClimatological.layerKey === undefined ||
          this.savedDataClimatological.layerKey === ""
        ) {
          alert("Layer Key cannot be Empty");
        } else {
          https
            .post(
              "/api/configuration/hazardconfigurations.xsjs",
              JSON.stringify(this.savedDataClimatological)
            )
            .then(
              response => {
                response.data !== 1 ? alert("Error Occured") : "";
                this.savedDataClimatological = {
                  type: "",
                  layerKey: "",
                  rangeFrom: "",
                  rangeTo: ""
                };
                this.loadClimatological();
              },
              error => {
                EventBus.$emit("Popup", {
                  popupType: "Error",
                  title: "Error Saving Hazard Values..!",
                  message: error.response.data.includes(
                    "unique constraint violated"
                  )
                    ? "301 - unique constraint violated"
                    : error
                });
              }
            );
        }
      }
      if (type === "others") {
        if (
          this.savedDataOthers.rangeFrom === null ||
          this.savedDataOthers.rangeFrom === undefined ||
          this.savedDataOthers.rangeFrom === "" ||
          this.savedDataOthers.rangeTo === null ||
          this.savedDataOthers.rangeTo === undefined ||
          this.savedDataOthers.rangeTo === ""
        ) {
          alert("Range Cannot be Empty");
        } else if (
          isNaN(this.savedDataOthers.rangeFrom) ||
          isNaN(this.savedDataOthers.rangeTo)
        ) {
          alert("Ranges should be Number");
        } else if (
          this.savedDataOthers.layerKey === null ||
          this.savedDataOthers.layerKey === undefined ||
          this.savedDataOthers.layerKey === ""
        ) {
          alert("Layer Key cannot be Empty");
        } else {
          https
            .post(
              "/api/configuration/hazardconfigurations.xsjs",
              JSON.stringify(this.savedDataOthers)
            )
            .then(
              response => {
                response.data !== 1 ? alert("Error Occured") : "";
                this.savedDataOthers = {
                  type: "",
                  layerKey: "",
                  rangeFrom: "",
                  rangeTo: ""
                };
                this.loadOthers();
              },
              error => {
                EventBus.$emit("Popup", {
                  popupType: "Error",
                  title: "Error Saving Hazard Values..!",
                  message: error.response.data.includes(
                    "unique constraint violated"
                  )
                    ? "301 - unique constraint violated"
                    : error
                });
              }
            );
        }
      }
    }
  },
  created() {
    https.get("/api/configuration/getToken.xsjs").then(
      () => {
        this.loadEarthquake();
        this.loadStorm();
        this.loadFlood();
        this.loadClimatological();
        this.loadOthers();
      },
      error => {
        EventBus.$emit("Popup", {
          popupType: "Error",
          title: "Un-Authorized",
          message: error
        });
      }
    );
  }
};
</script>

