        <label>Munich RE</label>
        <div id="login-env" v-show="isActual">
          <button :class="env === 'DEV' ? 'selected' : ''" @click="env = 'DEV'">DEV</button>
          <button :class="env === 'INT' ? 'selected' : ''" @click="env = 'INT'">INT</button>
          <button :class="env === 'PRD' ? 'selected' : ''" @click="env = 'PRD'">PRD</button>
        </div>
      </div>

      <input
        placeholder="Username"
        v-model="userName"
        @keyup.enter="authenticate()"
        v-show="isActual"
      >
      <input
        type="password"
        placeholder="Password"
        v-model="password"
        @keyup.enter="authenticate()"
        v-show="isActual"
      >
      <button
        @click="authenticate()"
        :disabled="userName === '' || password === ''"
        v-show="isActual"
      >Login</button>

      <button @click="loginto('DEV')">Development</button>
      <button @click="loginto('INT')">Integration</button>
      <button @click="loginto('PRD')">Production</button>
    </div>
    <div id="loaderloginframe" v-show="authinprocess">
      <div id="loader">
        <LoadSpinner/>
        <label>Authenticating...</label>
