<template>
  <div id="homepage">
    <div id="homepagesection">
      <div id="homepageheader">
        <Header/>
      </div>
      <div id="homepagecontent">
        <div id="clientsidebar">
          <div v-for="(item, idx) in clients" :key="idx" @click="setClient(item.id, item.clientid)">
            <label :class="currentClient === item.id ? 'selected' : ''">{{item.name}}</label>
          </div>
        </div>
        <div id="configsidebar">
          <div
            v-for="(item, gindex) in genericConfig"
            :key="item.id"
            @click="routeto(item.id, currentClient, currentClientId), currentConfig=gindex"
            v-show="currentClient === 'Generic'"
          >
            <label :class="currentConfig === gindex ? 'selected' : ''" :key="item.id">{{item.name}}</label>
          </div>
          <div
            v-for="(item, index) in clientConfig"
            :key="'c' + item.id"
            @click="routeto(item.id, currentClient, currentClientId), currentConfig=index"
            v-show="currentClient != 'Generic'"
          >
            <label :class="currentConfig === index ? 'selected' : ''">{{item.name}}</label>
          </div>
        </div>
        <div id="maincontent">
          <router-view/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Header from "@/components/Header";
import https from "@/https";
import { EventBus } from "@/event-bus.js";

export default {
  name: "homepage",
  props: {
    msg: String
  },
  components: {
    Header
  },
  data() {
    return {
      clients: [{ name: "Generic", id: "Generic", clientid: -1 }],
      genericConfig: [
        { name: "Dashboard", id: "dashboard" },
        { name: "Hazard Mapping", id: "hazardmapping" },
        { id: "crediantialmgmt", name: "Client Creation" },
        { id: "compareconfig", name: "Compare Config" },
        { id: "displaymodes", name: "Display Mode" },
        { id: "measures", name: "Measure" },
        { id: "hierarchies", name: "Hierarchies" },
        { id: "hierarchylevel", name: "Hierarchy Levels" },
        { id: "timelines", name: "Time Line" }
      ],
      clientConfig: [
        { id: "portfolio", name: "Portfolio(s)" },
        { id: "crediantialmgmt", name: "Crediantials" },
        { id: "dashboard", name: "Dashboard" }
      ],
      currentClient: "Generic",
      currentConfig: 0,
      currentClientId: -1
    };
  },

  methods: {
    async setClient(id, clientid) {
      this.currentConfig = 0;
      this.currentClient = id;
      this.currentClientId = clientid;
      await this.$router.push("/"); // just to mimic froce refresh of componenet
      clientid === -1
        ? this.routeto(
            this.genericConfig[0].id,
            this.currentClient,
            this.currentClientId
          )
        : this.routeto(
            this.clientConfig[0].id,
            this.currentClient,
            this.currentClientId
          );
    },
    routeto(id, client, clientid) {
      this.$router.push(
        "/" + id + "?client=" + client + "&clientid=" + clientid
      );
    },
    loadInitialData() {
      https.get("/api/configuration/usermgmt.xsjs?user=list").then(
        resolve => {
          resolve.data.forEach(element => {
            this.clients.push(element);
          });
        },
        error => {
          EventBus.$emit("Popup", {
            popupType: "Error",
            title: "Error",
            message: error
          });
        }
      );
    }
  },
  created() {
    if (this.$store.getters.isLogedin) {
      this.loadInitialData();
      this.routeto(
        this.clientConfig[this.currentConfig].id,
        this.currentClient,
        this.currentClientId
      );
    }

    EventBus.$on("loggedin", () => {
      this.loadInitialData();
    });

    EventBus.$on("AddClient", data => {
      this.clients.push(data);
    });

    EventBus.$on("UpdateClient", data => {
      let idx = this.clients.findIndex(x => x.id === data.id);
      this.clients[idx].name = data.name;
    });

    EventBus.$on("RemoveClient", data => {
      let idx = this.clients.findIndex(x => x.id === data.id);
      this.currentClient = this.clients[idx - 1].id;
      this.currentConfig = 0;
      this.routeto(
        this.genericConfig[this.currentConfig].id,
        this.currentClient,
        this.currentClientId
      );
      this.clients.splice(idx, 1);
    });
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped  >
@import "https://fonts.googleapis.com/css?family=Work+Sans|IBM+Plex+Sans|Lato|Signika|Bree+Serif|Barlow";

#homepage {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: row;
  background-color: #e6e7e8;
  justify-content: center;
}

#homepagesection {
  display: flex;
  flex-direction: column;
}

#homepageheader {
  min-height: 75px;
  background-color: #575757;
}

#homepagecontent {
  display: flex;
  height: calc(100% - 75px);
  width: 1600px;
  flex-direction: row;
}

#homepagecontent > #clientsidebar {
  display: block;
  flex-direction: column;
  justify-content: center;
  background-color: #cfcfcf;
  flex-basis: 240px;
  border-right: 1px solid #bdbbbb;
  overflow: auto;
}

#clientsidebar::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 5px;
  background-color: #ffffff;
}

#clientsidebar::-webkit-scrollbar {
  width: 8px;
  background-color: #ffffff;
}

#clientsidebar::-webkit-scrollbar-thumb {
  border-radius: 5px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: rgb(143, 142, 142);
}

#homepagecontent > #configsidebar {
  background-color: #e8e8e8;
  flex-basis: 240px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  border-right: 1px solid #bdbbbb;
}

#homepagecontent > #maincontent {
  background-color: #ffffff;
  flex-basis: 1120px;
  overflow-y: auto;
}

#homepagecontent > #maincontent::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 5px;
  background-color: #ffffff;
}

#homepagecontent > #maincontent::-webkit-scrollbar {
  width: 8px;
  height: 8px;
  background-color: #ffffff;
}

#homepagecontent > #maincontent::-webkit-scrollbar-thumb {
  border-radius: 5px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: rgb(143, 142, 142);
}

#clientsidebar > div,
#configsidebar > div {
  min-height: 40px;
  max-height: 40px;
  padding-left: 12px;
  font-family: "Bree Serif", "Signika", "IBM Plex Sans", sans-serif, Verdana,
    monospace;
  font-size: 1.35em;
  padding-top: 12px;
  white-space: nowrap;
}

#clientsidebar > div > label {
  display: block;
  padding-bottom: 8px;
  width: 200px;
  border-bottom: 3px solid;
  padding-left: 10px;
  padding-top: 6px;
  color: #3d4246;
  user-select: none;
  cursor: pointer;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

#clientsidebar > div > label:hover,
#clientsidebar > div > .selected {
  background-color: #4b4d5f;
  color: white;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
  border-bottom: 3px solid #22a2f2;
}

#clientsidebar > div > label:active {
  color: #3d4246;
  background-color: transparent;
  border-bottom: 3px solid #3d4246;
}

#clientsidebar > div > .selected,
#clientsidebar > div > .selected:hover,
#clientsidebar > div > .selected:active {
  border-bottom: 3px solid #06ff00;
  background-color: #4b4d5f;
  color: white;
}

#configsidebar div label {
  padding-left: 10px;
  font-family: "Signika", "IBM Plex Sans", sans-serif, Verdana;
  font-size: 1em;
  border: 1px solid;
  width: 88%;
  display: block;
  padding-top: 6px;
  padding-bottom: 4px;
  border-radius: 5px;
  user-select: none;
  color: #3d4246;
  overflow: hidden;
  text-overflow: ellipsis;
}

#configsidebar div label:hover,
#configsidebar div > .selected {
  background-color: #4b4d5f;
  color: white;
  border: 1px solid #4b4d5f;
  cursor: pointer;
}

#configsidebar div > .selected,
#configsidebar div > .selected:hover,
#configsidebar div > .selected:active {
  color: greenyellow;
  background-color: #4b4d5f;
}

#configsidebar div label:active {
  color: #3d4246;
  background-color: transparent;
}
</style>


