import Vue from "vue";
import VueRouter from "vue-router";
import HazardLayer from "@/views/HazardLayer.vue";
import ClientManagement from "@/views/ClientManagement.vue";
import Dashboard from "@/views/Dashboard.vue";
import Portfolio from "@/views/Portfolio.vue";
import CompareConfig from "@/views/CompareConfig.vue";
import DisplayModes from "@/views/DisplayModes.vue";
import Measures from "@/views/Measures.vue";
import Hierarchies from "@/views/Hierarchies.vue";
import Timelines from "@/views/Timelines.vue";
import HierarchiesLevels from "@/views/HierarchyLevel.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/dashboard",
    name: "Dashboard",
    component: Dashboard
  },
  {
    path: "/hazardmapping",
    name: "HazardLayerMapping",
    component: HazardLayer
  },
  {
    path: "/crediantialmgmt",
    name: "ClientManagement",
    component: ClientManagement
  },
  {
    path: "/portfolio",
    name: "Portfolio Selection",
    component: Portfolio
  },
  {
    path: "/compareconfig",
    name: "Compare Configuration",
    component: CompareConfig
  },
  {
    path: "/displaymodes",
    name: "Display Modes",
    component: DisplayModes
  },
  {
    path: "/measures",
    name: "Measures",
    component: Measures
  },
  {
    path: "/timelines",
    name: "Timeline",
    component: Timelines
  },
  {
    path: "/hierarchies",
    name: "Hierarchies",
    component: Hierarchies
  },
  {
    path: "/hierarchylevel",
    name: "Hierarchies Levels",
    component: HierarchiesLevels
  }
];

// eslint-disable-next-line no-new
const router = new VueRouter({
  mode: "abstract",
  routes: routes
});

