<template>
  <div id="portfolio-generalinfo">
    <div id="portfolio-gi-header">
      General Info
      <img src="@/assets/collapse.png" v-if="!collapse" @click="collapse = true">
      <img src="@/assets/expand.png" v-if="collapse" @click="collapse = false">
      <button
        :class=" edit ? 'save' : '' "
        @click="edit = !edit, collapse = false, savedata(edit) "
      >{{ edit ? 'Save' : 'Edit'}}</button>
    </div>
    <span id="portfolio-generalinfo-body" v-show="!collapse">
      <div id="portfolio-row1">
        <div>
          <label>Portfolio ID</label>::
          <input :value="portfolio.collectionId" readonly>
        </div>
        <div>
          <label>Display Name</label>::
          <input v-model="portfolio.displayName" :readonly="!edit">
        </div>
      </div>
      <div id="portfolio-row2">
        <div>
          <label>Portfolio Type</label>::
          <select name="type" v-model="portfolio.collectionType" :disabled="!edit">
            <option v-for="(item,index) in typeList" :key="index">{{item}}</option>
          </select>
        </div>
        <div>
          <label>Default Mode</label>::
          <select name="mode" v-model="portfolio.defaultDisplayMode" :disabled="!edit">
            <option v-for="(item,index) in modeList" :key="index">{{item}}</option>
          </select>
        </div>
        <div>
          <label>Default Measure</label>::
          <select name="measure" v-model="portfolio.defaultMeasure" :disabled="!edit">
            <option v-for="(item,index) in measureList" :key="index">{{item}}</option>
          </select>
        </div>
      </div>
      <div id="portfolio-row4">
        <div>
          <label>Uploaded By:</label>::
          <input :value="portfolio.userId" readonly>
        </div>
        <div>
          <label>{{portfolio.customPortfolio === 1 ? 'Custom' : 'Generic'}}</label>
        </div>
        <div>
          <label>Position</label>::
          <input v-model="portfolio.position" :readonly="!edit">
        </div>
      </div>
      <fieldset id="portfolio-row3">
        <legend>Bounding Boundry</legend>
        <div>
          <label>X-min</label>::
          <input v-model="portfolio.extentMinX" :readonly="!edit">
        </div>
        <div>
          <label>X-max</label>::
          <input v-model="portfolio.extentMaxX" :readonly="!edit">
        </div>
        <div>
          <label>Y-min</label>::
          <input v-model="portfolio.extentMinY" :readonly="!edit">
        </div>
        <div>
          <label>Y-max</label>::
          <input v-model="portfolio.extentMaxY" :readonly="!edit">
        </div>
      </fieldset>
    </span>
  </div>
</template>

<script>
import https from "@/https";
import { EventBus } from "@/event-bus.js";

export default {
  name: "portfolio-general-info",
  components: {},
  props: {
    clientid: Number,
    portfolioid: String,
    portfolioType: String
  },
  data() {
    return {
      portfolio: {},
      edit: false,
      typeList: ["EXPOSURE", "POLICY", "CLAIM"],
      modeList: ["CLUSTER", "REGIONS", "HEATMAP", "GRID"],
      measureList: [],
      defaultMeasure: "",
      defaultMode: "",
      collapse: false
    };
  },

  watch: {},
  methods: {
    savedata(edit) {
      if (!edit) {
        let payload = {};
        Object.keys(this.portfolio).forEach(x => {
          payload[x] = this.portfolio[x] === "" ? null : this.portfolio[x];
        });
        https
          .put(
            `/api/configuration/portfolio.xsjs?type=portfolio&clientid=${
              this.clientid
            }&portfolio=${this.portfolioid}&portfolioType=${this.portfolioType}`,
            JSON.stringify(payload)
          )
          .then(
            () => {
              EventBus.$emit("Popup", {
                popupType: "Information",
                title: "Saved Portfolio Details",
                message: "Saved Successfully..!",
                button1: false
              });
            },
            error =>
              EventBus.$emit("Popup", {
                popupType: "Error",
                title: "Error saving portfolio Details",
                message: error
              })
          );
      }
    }
  },
  created() {
    https
      .get(
        `/api/configuration/portfolio.xsjs?type=portfolio&clientid=${
          this.clientid
        }&portfolio=${this.portfolioid}&portfolioType=${this.portfolioType}`
      )
      .then(
        resolve => {
          this.portfolio = resolve.data;
          this.measureList.push(resolve.data.defaultMeasure);
        },
        error => {
          EventBus.$emit("Popup", {
            popupType: "Error",
            title: "Error fetching portfolio Details",
            message: error
          });
        }
      );
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@import "https://fonts.googleapis.com/css?family=Work+Sans|IBM+Plex+Sans|Signika|Bree+Serif|Roboto|Pacifico";

#portfolio-generalinfo {
  margin-top: 20px;
  margin-left: 10px;
}

#portfolio-generalinfo > #portfolio-gi-header {
  font-size: 1.2em;
  font-family: Pacifico, "IBM Plex Sans", sans-serif, Verdana;
  border-bottom: 2px solid #868683;
  padding-bottom: 4px;
  padding-left: 20px;
  color: #585753;
  margin-right: 30px;
  margin-bottom: 5px;
}

#portfolio-generalinfo > #portfolio-gi-header > img {
  float: right;
  height: 25px;
  width: 25px;
  margin-top: 8px;
  margin-right: 5px;
  border: 1px solid;
  border-radius: 5px;
}

#portfolio-generalinfo > #portfolio-gi-header > img:hover {
  border: 1px solid blue;
}

#portfolio-generalinfo > #portfolio-gi-header > button {
  float: right;
  margin-top: 10px;
  margin-right: 10px;
  width: 60px;
  height: 25px;
  border-radius: 30px;
  border: 0px;
  outline: none;
  font-family: Signika, "IBM Plex Sans", sans-serif, Verdana;
  font-weight: bold;
  letter-spacing: 0.05em;
  font-size: 0.8em;
  border: 1px solid #74736e;
  color: #403f3d;
  background-color: #c9c8c4;
}

#portfolio-generalinfo > #portfolio-gi-header > .save {
  background: #49ff58;
}

#portfolio-generalinfo > #portfolio-gi-header > button:hover {
  background-color: #403f3d;
  color: aliceblue;
}

#portfolio-generalinfo > #portfolio-gi-header > button:active {
  color: #403f3d;
  background-color: aliceblue;
}

#portfolio-generalinfo > #portfolio-generalinfo-body > #portfolio-row1,
#portfolio-generalinfo > #portfolio-generalinfo-body > #portfolio-row2,
#portfolio-generalinfo > #portfolio-generalinfo-body > #portfolio-row4 {
  display: flex;
  margin-top: 40px;
}

#portfolio-generalinfo > #portfolio-generalinfo-body > #portfolio-row1 > div,
#portfolio-generalinfo > #portfolio-generalinfo-body > #portfolio-row2 > div,
#portfolio-generalinfo > #portfolio-generalinfo-body > #portfolio-row4 > div {
  margin-right: 10px;
  display: flex;
  flex-direction: row;
  flex: 1;
}

#portfolio-generalinfo
  > #portfolio-generalinfo-body
  > #portfolio-row1
  > div
  > label,
#portfolio-generalinfo
  > #portfolio-generalinfo-body
  > #portfolio-row2
  > div
  > label,
#portfolio-generalinfo
  > #portfolio-generalinfo-body
  > #portfolio-row3
  > div
  > label,
#portfolio-generalinfo
  > #portfolio-generalinfo-body
  > #portfolio-row4
  > div
  > label {
  width: 120px;
  font-family: "Roboto", "IBM Plex Sans", sans-serif, Verdana;
  font-size: 0.9em;
  color: #585753;
  text-overflow: ellipsis;
}

#portfolio-generalinfo
  > #portfolio-generalinfo-body
  > #portfolio-row1
  > div
  > input,
#portfolio-generalinfo
  > #portfolio-generalinfo-body
  > #portfolio-row3
  > div
  > input,
#portfolio-generalinfo
  > #portfolio-generalinfo-body
  > #portfolio-row4
  > div
  > input {
  width: 350px;
  font-family: "Roboto", "IBM Plex Sans", sans-serif, Verdana;
  font-size: 1em;
  padding-left: 10px;
  text-overflow: ellipsis;
  overflow: hidden;
  border: 0px;
  border-bottom: 2px solid #585753;
  outline: none;
  margin-left: 5px;
}

#portfolio-generalinfo
  > #portfolio-generalinfo-body
  > #portfolio-row1
  > div
  > input:focus,
#portfolio-generalinfo
  > #portfolio-generalinfo-body
  > #portfolio-row2
  > div
  > select:focus,
#portfolio-generalinfo
  > #portfolio-generalinfo-body
  > #portfolio-row3
  > div
  > input:focus,
#portfolio-generalinfo
  > #portfolio-generalinfo-body
  > #portfolio-row4
  > div
  > input:focus {
  border-bottom: 2px solid blue;
}

#portfolio-generalinfo
  > #portfolio-generalinfo-body
  > #portfolio-row1
  > div
  > input:read-only,
#portfolio-generalinfo
  > #portfolio-generalinfo-body
  > #portfolio-row2
  > div
  > select:disabled,
#portfolio-generalinfo
  > #portfolio-generalinfo-body
  > #portfolio-row3
  > div
  > input:read-only,
#portfolio-generalinfo
  > #portfolio-generalinfo-body
  > #portfolio-row4
  > div
  > input:read-only {
  border-bottom: 2px solid #585753;
  color: #868582;
}

#portfolio-generalinfo
  > #portfolio-generalinfo-body
  > #portfolio-row2
  > div
  > select {
  height: 22px;
  width: 180px;
  font-family: "Roboto", "IBM Plex Sans", sans-serif, Verdana;
  font-size: 1em;
  padding-left: 5px;
  text-overflow: ellipsis;
  overflow: hidden;
  border: 0px;
  border-bottom: 2px solid;
  outline: none;
}

#portfolio-generalinfo > #portfolio-generalinfo-body > #portfolio-row3 {
  margin-top: 30px;
  border-radius: 5px;
  margin-bottom: 35px;
}

#portfolio-generalinfo > #portfolio-generalinfo-body > #portfolio-row3 > div {
  display: inline;
  margin-right: 10px;
}

#portfolio-generalinfo
  > #portfolio-generalinfo-body
  > #portfolio-row3
  > legend {
  font-family: "Work Sans", "IBM Plex Sans", sans-serif, Verdana;
  font-size: 0.9em;
}

#portfolio-generalinfo
  > #portfolio-generalinfo-body
  > #portfolio-row3
  > div
  > input {
  width: 170px;
}

#portfolio-generalinfo
  > #portfolio-generalinfo-body
  > #portfolio-row4
  > div:nth-child(3)
  > input {
  width: 75px;
  text-align: center;
}

#portfolio-generalinfo
  > #portfolio-generalinfo-body
  > #portfolio-row4
  > div:nth-child(2)
  > label {
  border-radius: 30px;
  border: 1px solid;
  text-align: center;
  width: 80px;
  font-weight: bold;
  letter-spacing: 0.09em;
  padding-top: 1px;
  background-color: #ffbd1f;
}
</style>
