<template>
  <div id="portfolio-displaymode">
    <div id="portfolio-dm-header">
      Display Modes
      <img src="@/assets/collapse.png" v-if="!collapse" @click="collapse = true">
      <img src="@/assets/expand.png" v-if="collapse" @click="collapse = false">
      <button
        :class=" edit ? 'save' : '' "
        @click="edit = !edit, collapse = false, updateDisplayMode(edit)"
      >{{ edit ? 'Save' : 'Edit'}}</button>
    </div>
    <span id="portfolio-displaymode-body" v-show="!collapse">
      <div id="portfolio-dm-row-head">
        <div>Technical ID</div>
        <div>Display Name</div>
        <div>Position</div>
        <div>Status</div>
      </div>
      <div id="portfolio-dm-row" v-for="(item, index) in displayMode" :key="index">
        <div>{{item.displayModeId}}</div>
        <div>{{item.displayName}}</div>
        <div>
          <input :placeholder="item.defaultPosition" v-model="item.userPosition" :readonly="!edit">
        </div>
        <div>
          <label class="switch">
            <input type="checkbox" :disabled="!edit"  :checked="item.userSelected !== '0' " @change="update(index, $event)">
            <span class="slider round"></span>
          </label>
        </div>
      </div>
    </span>
  </div>
</template>

<script>
import https from "@/https";
import { EventBus } from "@/event-bus.js";

export default {
  name: "portfolio-display-mode",
  components: {},
  props: {
    clientid: Number,
    portfolioid: String,
    portfolioType: String
  },
  data() {
    return {
      edit: false,
      collapse: false,
      displayMode: [],
      changes: []
    };
  },

  watch: {},
  methods: {
    updateDisplayMode(edit) {
      if (!edit) {
        let payload = [];

        Object.keys(this.displayMode).forEach(x => {
          if (this.displayMode[x].userSelected !== "0") {
            payload.push({
              displayModeId: this.displayMode[x]["displayModeId"],
              userPosition: this.displayMode[x]["userPosition"]
            });
          }
        });

        https
          .put(
            `/api/configuration/portfolio.xsjs?type=displaymode&clientid=${
              this.clientid
            }&portfolio=${this.portfolioid}&portfolioType=${this.portfolioType}`,
            JSON.stringify(payload)
          )
          .then(
            () => {
              EventBus.$emit("Popup", {
                popupType: "Information",
                title: "Saved Portfolio Details",
                message: "Saved Successfully..!",
                button1: false
              });
            },
            error =>
              EventBus.$emit("Popup", {
                popupType: "Error",
                title: "Error saving portfolio Details",
                message: error
              })
          );
      }
    },
    update(idx, event) {
      if(event.target.checked)
      {
        this.displayMode[idx].userSelected = this.displayMode[idx].displayModeId;
        this.displayMode[idx].userPosition =
          this.displayMode[idx].userPosition === null ||
          this.displayMode[idx].userPosition === ""
            ? this.displayMode[idx].defaultPosition
            : this.displayMode[idx].userPosition;
      }
      else
      {
        this.displayMode[idx].userSelected = "0";
        this.displayMode[idx].userPosition = this.displayMode[idx].defaultPosition;
      }
    }
    
  },
  created() {
    https
      .get(
        `/api/configuration/portfolio.xsjs?type=displaymode&clientid=${
          this.clientid
        }&portfolio=${this.portfolioid}&portfolioType=${this.portfolioType}`
      )
      .then(
        resolve => (this.displayMode = resolve.data),
        error => {
          EventBus.$emit("Popup", {
            popupType: "Error",
            title: "Error fetching display modes",
            message: error
          });
        }
      );
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
