<template>
  <div id="portfolio-timeline">
    <div id="portfolio-timeline-header">
      Timeline
      <img src="@/assets/collapse.png" v-if="!collapse" @click="collapse = true">
      <img src="@/assets/expand.png" v-if="collapse" @click="collapse = false">
      <button
        :class=" edit ? 'save' : '' "
        @click="edit = !edit, collapse = false, updateTimelineMode(edit)"
      >{{ edit ? 'Save' : 'Edit'}}</button>
    </div>
    <span id="portfolio-timeline-body" v-show="!collapse">
      <div id="portfolio-timeline-row-head">
        <div>Timeline ID</div>
        <div>Display Name</div>
        <div>Timeline Mode</div>
        <div>Timeline Scale</div>
        <div>Position</div>
        <div>Status</div>
      </div>
      <div id="portfolio-timeline-row" v-for="(item, index) in timeline" :key="index">
        <div>{{item.timelineId}}</div>
        <div>{{item.displayName}}</div>
        <div>{{item.timelineMode}}</div>
        <div>{{item.timelineScaleDefault}}</div>
        <div>
          <input
            :placeholder="item.defaultPosition"
            v-model="item.userSelPosition"
            :readonly="!edit"
          >
        </div>
        <div>
          <!-- <button
            :disabled="!edit"
            v-show="item.userSelected === 0"
            @click="item.userSelected = 1"
          >{{ 'Enable' }}</button>
          <button
            :disabled="!edit"
            class="enabled"
            v-show="item.userSelected === 1"
            @click="item.userSelected = 0"
          >{{ 'Disable' }}</button> -->
          <label class="switch">
           <!--  <input type="checkbox" :disabled="!edit"  :checked="item.userSelected !== 0 " @change="update(index, $event)"> -->
           <input type="checkbox" :disabled="!edit"  v-model="item.userSelected">
            <span class="slider round"></span>
          </label>
        </div>
      </div>
    </span>
  </div>
</template>

<script>
import https from "@/https";
import { EventBus } from "@/event-bus.js";

export default {
  name: "portfolio-timeline",
  components: {},
  props: {
    clientid: Number,
    portfolioid: String,
    portfolioType: String
  },
  data() {
    return {
      edit: false,
      collapse: false,
      timeline: []
    };
  },

  watch: {},
  methods: {
    updateTimelineMode(edit) {
      if (!edit) {
        let payload = [];

        Object.keys(this.timeline).forEach(x => {
          if (this.timeline[x].userSelected) {
            payload.push({
              timeLineId: this.timeline[x]["timelineId"],
              userPosition: this.timeline[x]["userSelPosition"] === null ? this.timeline[x]["defaultPosition"] : this.timeline[x]["userSelPosition"]
            });
          }
        });

        https
          .put(
            `/api/configuration/portfolio.xsjs?type=timeline&clientid=${
              this.clientid
            }&portfolio=${this.portfolioid}&portfolioType=${this.portfolioType}`,
            JSON.stringify(payload)
          )
          .then(
            () => {
              EventBus.$emit("Popup", {
                popupType: "Information",
                title: "Saved Portfolio Details",
                message: "Saved Successfully..!",
                button1: false
              });
            },
            error =>
              EventBus.$emit("Popup", {
                popupType: "Error",
                title: "Error saving portfolio Details",
                message: error
              })
          );
      }
    }
  },
  created() {
    https
      .get(
        `/api/configuration/portfolio.xsjs?type=timeline&clientid=${
          this.clientid
        }&portfolio=${this.portfolioid}&portfolioType=${this.portfolioType}`
      )
      .then(
        resolve => {
          this.timeline = resolve.data;
        },
        error => {
          EventBus.$emit("Popup", {
            popupType: "Error",
            title: "Error fetching Timelines",
            message: error
          });
        }
      );
  }
};
</script>

