/*******************Toggle Switch **************/
.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 25px;
}

.switch input { 
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 21px;
  width: 21px;
  left: 2px;
  bottom: 2px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: #299432;
}

input:checked + .slider:before {
  -webkit-transform: translateX(35px);
  -ms-transform: translateX(35px);
  transform: translateX(35px);
}



/* Rounded sliders */
.slider.round {
  border-radius: 21px;
}

.slider.round:before {
  border-radius: 50%;
}

.slider.round:after {
  content: "OFF";
  margin-left: 15px;
  font-family: "Signika", "IBM Plex Sans", sans-serif, Verdana;
  font-size: 0.8em;
  letter-spacing: 0.03em;
  font-weight: bold;
  color:#585753
}

input:checked + .slider.round:after {
  content: "ON";
  padding-right: 50px;
  font-family: "Signika", "IBM Plex Sans", sans-serif, Verdana;
  font-size: 0.8em;
  letter-spacing: 0.03em;
  font-weight: bold;
  color:#49ff58;
  
}

input:disabled + .slider:before  {
  background-color:#74726e
}
