<template>
  <div id="portfolio">
    <div id="portfolio-bar" v-for="(item,index) in portfolios" :key="index">
      <div id="portfolio-bar-head" v-show="currentIndex === index || currentIndex === ''">
        <div>
          <label>Name</label>
          <input v-model="item.displayName" readonly>
        </div>
        <div>
          <label>Type</label>
          <input v-model="item.collectionType" readonly>
        </div>
        <div>
          <label>Default Mode</label>
          <input v-model="item.defaultDisplayMode" readonly>
        </div>
        <div>
          <label>Default Measure</label>
          <input v-model="item.defaultMeasure" readonly>
        </div>
        <div>
          <label>Position</label>
          <input v-model="item.position" readonly>
        </div>
        <div>
          <label
            :style="item.customPortfolio === 1 ? 'color:#D7282F' : 'color:#283640'"
          >{{ item.customPortfolio === 1 ? 'Custom' : 'Generic' }}</label>
          <button
            @click="currentIndex = currentIndex === index ? '' : index"
          >{{currentIndex === index ? 'Back' : 'Configure' }}</button>
        </div>
      </div>
      <div id="portfolio-bar-content" v-if="currentIndex === index">
        <GeneralInfo :clientid="item.CLIENT" :portfolioid="item.collectionId" :portfolioType="item.collectionType"/>
        <DisplayMode :clientid="item.CLIENT" :portfolioid="item.collectionId" :portfolioType="item.collectionType"/>
        <Measures :clientid="item.CLIENT" :portfolioid="item.collectionId" :portfolioType="item.collectionType"/>
        <Hierarchies :clientid="item.CLIENT" :portfolioid="item.collectionId" :portfolioType="item.collectionType"/>
        <Timeline :clientid="item.CLIENT" :portfolioid="item.collectionId" :portfolioType="item.collectionType"/>
      </div>
    </div>
  </div>
</template>

<script>
import https from "@/https";
import { EventBus } from "@/event-bus.js";
import GeneralInfo from "@/components/PortfolioGeneralInfo";
import DisplayMode from "@/components/PortfolioDisplayMode";
import Measures from "@/components/PortfolioMeasures";
import Hierarchies from "@/components/PortfolioHierarchy";
import Timeline from "@/components/PortfolioTimeline";

export default {
  name: "portfolio",
  components: {
    GeneralInfo,
    DisplayMode,
    Measures,
    Hierarchies,
    Timeline
  },
  data() {
    return {
      client: "test",
      clientid: -1,
      portfolios: [],
      currentIndex: ""
    };
  },

  watch: {},
  methods: {},
  async created() {
    this.client = this.$route.query.client;
    this.clientid = this.$route.query.clientid;
    https
      .get(
        "/api/configuration/portfolio.xsjs?type=portfolio&clientid=" +
          this.$route.query.clientid
      )
      .then(
        resolve => {
          this.portfolios = resolve.data;
        },
        error => {
          EventBus.$emit("Popup", {
            popupType: "Error",
            title: "Error fetching portfolios",
            message: error
          });
        }
      );
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@import "https://fonts.googleapis.com/css?family=Work+Sans|IBM+Plex+Sans|Signika|Bree+Serif|Roboto|Pacifico";

#portfolio {
  display: flex;
  flex-direction: column;
  margin-left: 30px;
  margin-top: 20px;
}

#portfolio > #portfolio-bar {
  margin-right: 30px;
}

#portfolio > #portfolio-bar > #portfolio-bar-head {
  border: 1px solid;
  margin-top: 6px;
  height: 55px;
  display: flex;
  flex-direction: row;
  padding-left: 12px;
  padding-right: 12px;
  border-radius: 5px;
  background-color: #bbbbbb;
}

#portfolio > #portfolio-bar > #portfolio-bar-head > div {
  display: flex;
  flex-direction: column;
  flex: 1;
  margin-top: 8px;
}

#portfolio > #portfolio-bar > #portfolio-bar-head > div:last-child {
  margin-top: 2px;
}

#portfolio > #portfolio-bar > #portfolio-bar-head > div:first-child {
  flex: 6;
  margin-right: 6px;
}

#portfolio > #portfolio-bar > #portfolio-bar-head > div:nth-child(4) {
  margin-right: 4px;
}

#portfolio > #portfolio-bar > #portfolio-bar-head > div:last-child {
  flex: 2;
}

#portfolio > #portfolio-bar > #portfolio-bar-head > div > label {
  font-family: "Roboto", "IBM Plex Sans", sans-serif, Verdana;
  font-size: 0.8em;
  color: #656565;
}

#portfolio > #portfolio-bar > #portfolio-bar-head > div > input {
  font-family: "IBM Plex Sans", sans-serif, Verdana;
  font-size: 1em;
  color: #656565;
  border: 0px;
  outline: none;
  width: 150px;
  font-weight: bold;
  background-color: transparent;
  color: black;
  overflow: hidden;
  text-overflow: ellipsis;
}
#portfolio > #portfolio-bar > #portfolio-bar-head > div:first-child > input {
  width: auto;
  text-overflow: ellipsis;
}

#portfolio > #portfolio-bar > #portfolio-bar-head > div:last-child > button {
  height: 25px;
  border-radius: 5px;
  border: 0px;
  outline: none;
  user-select: none;
  font-family: "Signika", "Work Sans", "IBM Plex Sans", sans-serif, Verdana;
  font-size: 1em;
  color: #284aaf;
  letter-spacing: 1px;
  border: 1px solid #284aaf;
  font-weight: bold;
  background-color: #00ff12;
}

#portfolio
  > #portfolio-bar
  > #portfolio-bar-head
  > div:last-child
  > button:hover {
  background-color: #ff9f0d;
}

#portfolio
  > #portfolio-bar
  > #portfolio-bar-head
  > div:last-child
  > button:active {
  border: 1px solid #283640;
  color: #283640;
  background-color: #d9d2c9;
}

#portfolio > #portfolio-bar > #portfolio-bar-head > div:last-child > label {
  margin-bottom: 6px;
  border: 1px solid;
  text-align: center;
  width: 60px;
  border-radius: 30px;
  margin-left: 20px;
  font-family: "Roboto", "IBM Plex Sans", sans-serif, Verdana;
  background-color: #efedd1;
  letter-spacing: 0.05em;
  color: #283640;
  user-select: none;
}

#portfolio > #portfolio-bar > #portfolio-bar-content {
  border-left: 1px dotted #aaa99a;
  border-top: 0px;
  padding-bottom: 40px;
}
</style>
