<template>
  <div id="compare-config">
    <div id="back-button">
      <button @click="stepback()" v-show="envSelected !== -1 || tableSelected !== ''">Go Back</button>
    </div>
    <label id="header" v-show="tableSelected === ''">Compare Configuration Tables</label>
    <div id="body" v-show="tableSelected === ''">
      <transition name="slide-fade">
        <div id="env" v-show="envSelected === -1">
          <label>Select the envoirnment from which you want to compare the data. (you may need to login)</label>
          <span v-for="(item, index) in env" :key="index + '-env'">
            <button @click="authenticate(index)" :disabled="envSelected >= 0">{{item.name}}</button>
          </span>
        </div>
      </transition>
      <transition name="slide-fade-in">
        <div id="tables" v-show="envSelected >= 0">
          <label>Select a table to compare</label>
          <div>
            <span v-for="(item, index) in tables" :key="index + '-table'">
              <button @click="tableSelected = item" :disabled="tableSelected !== ''">{{item}}</button>
            </span>
          </div>
        </div>
      </transition>
    </div>
    <Compare :srcEnv="env[envSelected].id" :table="tableSelected" v-if="tableSelected !== ''"/>
  </div>
</template>

<script>
import { EventBus } from "@/event-bus.js";
import https from "@/https";
import Compare from "@/components/CompareConfig";

export default {
  name: "compare-config",
  components: {
    Compare
  },
  data() {
    return {
      tableSelected: "",
      envSelected: -1,
      env: [
        { id: "INT", name: "Integration" },
        { id: "PRD", name: "Production" },
        { id: "DEV", name: "Development" }
      ],
      tables: [
        "CUST_COLLECTIONS",
        "CUST_COLLECTIONS_CUSTOMIZING",
        "CUST_COLLECTION_MEASURES",
        "CUST_COLLECTION_PARAMETERS",
        "CUST_DISPLAY_MODES",
        "CUST_HIERARCHIES",
        "CUST_HIERARCHY_LEVEL",
        "CUST_TIMELINES",
        "MAP_INP_LAYER_HAZARD"
      ]
    };
  },

  watch: {},
  methods: {
    stepback() {
      if (this.tableSelected !== "") {
        this.tableSelected = "";
      } else {
        this.tableSelected = "";
        this.envSelected = -1;
      }
    },
    authenticate(idx) {
      https
        .get(
          `${
            this.$store.getters.getSysAddr(this.env[idx].id).host
          }/api/configuration/getToken.xsjs`
        )
        .then(
          () => {
            this.envSelected = idx;
          },
          error => {
            EventBus.$emit("Popup", {
              popupType: "Error",
              title: "Un-Authorized",
              message: error
            });
          }
        );
    }
  },
  created() {
    let x = this.$store.getters.getCurrentSys;
    if (x === "DEV") {
      this.env = [
        { id: "INT", name: "Integration" },
        { id: "PRD", name: "Production" }
      ];
    }
    if (x === "INT") {
      this.env = [
        { id: "DEV", name: "Development" },
        { id: "PRD", name: "Production" }
      ];
    }
    if (x === "PRD") {
      this.env = [
        { id: "DEV", name: "Development" },
        { id: "INT", name: "Integration" }
      ];
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@import "https://fonts.googleapis.com/css?family=Work+Sans|IBM+Plex+Sans|Signika|Bree+Serif|Roboto|Pacifico";

#compare-config {
  margin-left: 30px;
  margin-top: 10px;
  margin-right: 30px;
  display: flex;
  flex-direction: column;
}

#compare-config > #back-button > button {
  float: right;
  border-radius: 5px;
  padding-left: 10px;
  padding-right: 10px;
  font-family: "Signika", "IBM Plex Sans", sans-serif, Verdana, monospace;
  outline: none;
  border: 0px;
  background-color: #d9d9d9;
}

#compare-config > #back-button > button:hover {
  background-color: #bfbfbf;
}
#compare-config > #back-button > button:active {
  background-color: #d9d9d9;
}

#compare-config > #header {
  font-family: "Pacifico", "IBM Plex Sans", sans-serif, Verdana, monospace;
  font-size: 1.8em;
  border-bottom: 2px solid #868683;
  padding-bottom: 3px;
  color: #585753;
  margin-top: 50px;
}

#compare-config > #body {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

#compare-config > #body > #env,
#compare-config > #body > #tables {
  margin-top: 60px;
  width: 450px;
  min-height: 300px;
  border-radius: 20px;
  -webkit-box-shadow: 1px 2px 6px 5px rgba(0, 0, 0, 0.29);
  box-shadow: 1px 2px 6px 5px rgba(0, 0, 0, 0.29);
  padding-top: 20px;
  display: flex;
  flex-direction: column;
}

#compare-config > #body > #env > label,
#compare-config > #body > #tables > label {
  margin-left: 10px;
  margin-right: 10px;
  padding-left: 15px;
  padding-right: 15px;
  font-family: "Signika", "IBM Plex Sans", sans-serif, Verdana, monospace;
  font-size: 1.3em;
  border-bottom: 2px solid;
  padding-bottom: 3px;
  margin-bottom: 25px;
}
#compare-config > #body > #env > span {
  display: flex;
  justify-content: center;
  padding-top: 10px;
}

#compare-config > #body > #env > span {
  display: flex;
  justify-content: center;
  padding-top: 10px;
}

#compare-config > #body > #env > span > button {
  width: 180px;
  height: 60px;
  border-radius: 10px;
  font-family: "IBM Plex Sans", sans-serif, Verdana, monospace;
  font-size: 1.3em;
  font-weight: bold;
  outline: none;
  user-select: none;
  background-color: #0dff91;
  border: 1px solid #0dff91;
}
#compare-config > #body > #env > span > button:hover {
  background-color: #0ce846;
}

#compare-config > #body > #env > span > button:active {
  background-color: #0dff91;
}

.slide-fade-enter-active {
  transition: all 0.4s ease;
}
.slide-fade-leave-active {
  transition: all 0.4s cubic-bezier(1.5, 1.5, 3, 3);
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active below version 2.1.8 */ {
  transform: translateX(-500px);
  opacity: 0;
}

.slide-fade-in-enter-active {
  transition: all 0.4s ease;
}
.slide-fade-in-leave-active {
  transition: all 1.2s cubic-bezier(3, 3, 3, 3);
}
.slide-fade-in-enter, .slide-fade-in-leave-to
/* .slide-fade-leave-active below version 2.1.8 */ {
  transform: translateX(200px);
}

#compare-config > #body > #tables {
  width: 800px;
  padding-bottom: 20px;
}

#compare-config > #body > #tables > div {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding-left: 20px;
  padding-right: 20px;
}

#compare-config > #body > #tables > div > span > button {
  flex: 1;
  margin-left: 10px;
  margin-top: 10px;
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 3px;
  padding-bottom: 3px;
  height: 32px;
  border-radius: 5px;
  border: 0px;
  font-family: "IBM Plex Sans", sans-serif, Verdana, monospace;
  font-size: 1.1em;
  font-weight: bold;
  outline: none;
  user-select: none;
  background-color: #0dff91;
  border: 1px solid #0dff91;
}
#compare-config > #body > #tables > div > span > button:hover {
  background-color: #0ce846;
}

#compare-config > #body > #tables > div > span > button:active {
  background-color: #0dff91;
}
</style>
