<template>
  <div id="hierarchies">
    <label id="header">
      Hierarchies
      <span>[{{displayMode.length}}]</span>
      <button @click="addnew = !addnew">{{ addnew ? 'x' :'+'}}</button>
    </label>
    <div id="body">
      <div id="tab-head">
        <label v-for="(item, idx) in columns" :key="'th-' + idx">{{item.name}}</label>
        <label>Actions</label>
      </div>

      <div id="tab-add" v-show="addnew">
        <div v-for="(field, cidx) in columns" :key="'tdi-' + cidx">
          <input v-model="field.value" placeholder="New Value">
        </div>
        <div>
          <label @click="insertnew()">Add</label>
        </div>
      </div>

      <div
        id="tab-content"
        v-for="(drow, ridx) in displayMode"
        :key="'tr-' + ridx"
        :style=" save[ridx].save ? 'border-color: blue' : ''"
      >
        <div v-for="(field, cidx) in columns" :key="'td-' + cidx">
          <input v-model="drow[field.id]" :readonly="iskey(field.id) || !save[ridx].save">
        </div>
        <div>
          <label
            v-if="save[ridx] && save[ridx].save"
            @click="save[ridx].save = false, update(ridx)"
          >S</label>
          <label v-if="save[ridx] && !save[ridx].save" @click="save[ridx].save = true">M</label>
          <label @click="remove(ridx)">D</label>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { EventBus } from "@/event-bus.js";
import https from "@/https";

export default {
  name: "hierarchies",
  components: {},
  data() {
    return {
      addnew: false,
      save: [],
      insert: [],
      columns: [
        { id: "CLIENT", name: "Client", value: "" },
        { id: "hierarchyId", name: "Hierarchy Id", value: "" },
        { id: "dbColumn", name: "DB Column", value: "" },
        { id: "displayName", name: "Display Name", value: "" }
      ],
      keys: [],
      displayMode: []
    };
  },

  watch: {},
  methods: {
    insertnew() {
      let key = [];
      this.columns
        .filter(x => this.keys.includes(x.id))
        .forEach(x =>
          x.value !== "" && x.value !== undefined && x.value !== null
            ? key.push(true)
            : key.push(false)
        );
      if (key.every(x => x === true)) {
        let payload = {};
        this.columns.forEach(
          x => (payload[x.id] = x.value === "" ? null : x.value)
        );

        https
          .post(
            "/api/configuration/compare.xsjs?table=CUST_HIERARCHIES",
            JSON.stringify([payload])
          )
          .then(
            resolve => {
              if (resolve.data.status === 200) {
                EventBus.$emit("Popup", {
                  popupType: "Information",
                  title: "Adding hierarchies",
                  message: resolve.data.status + " : " + resolve.data.message,
                  button1: false
                });
                this.loadData();

                this.columns.forEach(x => (x.value = ""));
                this.addnew = false;
              } else
                EventBus.$emit("Popup", {
                  popupType: "Error",
                  title: "Adding hierarchies",
                  message: resolve.data.status + " : " + resolve.data.message
                });
            },
            error =>
              EventBus.$emit("Popup", {
                popupType: "Error",
                title: "Error adding hierarchies",
                message: error
              })
          );
      } else {
        EventBus.$emit("Popup", {
          popupType: "Error",
          title: "Error adding hierarchies",
          message: "Key Fields cannot be Empty"
        });
      }
    },
    update(idx) {
      let payload = {};
      Object.keys(this.displayMode[idx]).forEach(
        x =>
          (payload[x] =
            this.displayMode[idx][x] === "" ? null : this.displayMode[idx][x])
      );
      https
        .put(
          "/api/configuration/compare.xsjs?table=CUST_HIERARCHIES",
          JSON.stringify({
            keys: this.keys,
            data: [payload]
          })
        )
        .then(
          () => {},
          error =>
            EventBus.$emit("Popup", {
              popupType: "Error",
              title: "Error updating hierarchies",
              message: error
            })
        );
    },
    remove(idx) {
      let self = this;
      let remove = function() {
        let payload = {};
        self.keys.forEach(x => (payload[x] = self.displayMode[idx][x]));
        https
          .delete("/api/configuration/compare.xsjs?table=CUST_HIERARCHIES", {
            data: JSON.stringify([payload])
          })
          .then(
            () => {
              self.displayMode.splice(idx, 1);
              self.save.splice(idx, 1);
            },
            error =>
              EventBus.$emit("Popup", {
                popupType: "Error",
                title: "Error Deleting hierarchies",
                message: error
              })
          );
      };
      EventBus.$emit("Popup", {
        popupType: "Confirmation",
        title: "Remove Display Config",
        message: "Are you sure you want to remove this configuration?",
        confirmFunction: remove
      });
    },
    iskey(id) {
      return this.keys.indexOf(id) >= 0;
    },
    getSaveStatus(idx) {
      return this.save.includes(idx);
    },
    loadData() {
      https.get("/api/configuration/compare.xsjs?table=CUST_HIERARCHIES").then(
        resolve => {
          this.keys = resolve.data.keys;
          this.displayMode = resolve.data.data;
          resolve.data.data.forEach(() => this.save.push({ save: false }));
        },
        error =>
          EventBus.$emit("Popup", {
            popupType: "Error",
            title: "Error fetching hierarchies",
            message: error
          })
      );
    }
  },
  created() {
    this.loadData();
  }
};
</script>

