#hierarchies > #body > #tab-head > label:nth-child(2),
#hierarchies > #body > #tab-head > label:nth-child(3),
#hierarchies > #body > #tab-content > div:nth-child(2),
#hierarchies > #body > #tab-content > div:nth-child(3),
#hierarchies > #body > #tab-add > div:nth-child(2),
#hierarchies > #body > #tab-add > div:nth-child(3) {
