<template>
  <div id="compare">
    <div id="info">
      <td>
        <label>Table :</label>
        <label>{{table}}</label>
      </td>
      <td>
        <label>Num Of Rec :</label>
        <label>{{srcEnv}} : {{src.length}} / {{this.$store.getters.getCurrentSys}}: {{dest.length}}</label>
      </td>
      <td>
        <button
          :disabled="isProcessed > 0 || src.length === 0 "
          @click="process(src,dest,keys)"
        >Compare</button>
      </td>
    </div>

    <div class="headers" v-show="insert.length > 0">
      <label>New Records [{{insert.length}}]</label>
      <label>Records from {{srcEnv}}</label>
      <img src="@/assets/collapse.png" v-show="insertExpand" @click="insertExpand = false">
      <img src="@/assets/expand.png" v-show="!insertExpand" @click="insertExpand = true">
      <button @click="insertData()">Insert All</button>
      <button @click="insertData(insertSelect)" v-show="insert.length > 1">Insert Selected</button>
    </div>

    <span v-for="(row,irid) in insert" :key="'i'+irid" v-show="insertExpand">
      <div>
        <input type="checkbox" v-model="insertSelect" :value="irid">
        <div v-for="(key,ikid) in Object.keys(row)" :key="'ik'+ikid" class="row">
          <label class="rowitemkey">{{key}}</label>
          <label class="rowitemvalue">{{row[key]}}</label>
        </div>
      </div>
    </span>

    <div class="headers" v-show="modify.length > 0">
      <label>Modified Records [{{modify.length}}]</label>
      <label>Records from {{srcEnv}}</label>
      <img src="@/assets/collapse.png" v-show="modifyExpand" @click="modifyExpand = false">
      <img src="@/assets/expand.png" v-show="!modifyExpand" @click="modifyExpand = true">
      <button @click="updateData()">Update All</button>
      <button @click="updateData(modifySelect)" v-show="modify.length > 1">Update Selected</button>
    </div>

    <span v-for="(row,mrid) in modify" :key="'m'+mrid" v-show="modifyExpand">
      <div>
        <input type="checkbox" v-model="modifySelect" :value="mrid">
        <div v-for="(key,mkid) in Object.keys(row)" :key="'mk'+mkid" class="row">
          <label class="rowitemkey">{{key}}</label>
          <label class="rowitemvalue">{{row[key]}}</label>
        </div>
      </div>
    </span>

    <div class="headers" v-show="del.length > 0">
      <label>Extra Records [{{del.length}}]</label>
      <label>Records from {{this.$store.getters.getCurrentSys}}</label>
      <img src="@/assets/collapse.png" v-show="delExpand" @click="delExpand = false">
      <img src="@/assets/expand.png" v-show="!delExpand" @click="delExpand = true">
      <button @click="delData()">Delete All</button>
      <button @click="delData(delSelect)" v-show="del.length > 1">Delete Selected</button>
    </div>

    <span v-for="(row,erid) in del" :key="'e'+erid" v-show="delExpand">
      <div>
        <input type="checkbox" v-model="delSelect" :value="erid">
        <div v-for="(key,ekid) in Object.keys(row)" :key="'ek'+ekid" class="row">
          <label class="rowitemkey">{{key}}</label>
          <label class="rowitemvalue">{{row[key]}}</label>
        </div>
      </div>
    </span>

    <span v-if="startComparing === 1 && isProcessed === 0">
      <div id="Notice">
        <LoadSpinner/>
        <label>Comparing</label>
      </div>
    </span>
    <span>
      <div
        id="Notice"
        v-if="isProcessed === 1 && insert.length === 0 && modify.length === 0 && del.length === 0"
      >
        <label>No Changes found...</label>
      </div>
      <div id="Notice" v-if="src.length === 0">
        <label>No data to compare...!</label>
      </div>
    </span>
  </div>
</template>

<script>
import { EventBus } from "@/event-bus.js";
import https from "@/https";
import LoadSpinner from "@/components/LoadSpinner";

export default {
  name: "compare",
  components: { LoadSpinner },
  props: {
    srcEnv: String,
    table: String
  },
  data() {
    return {
      insertExpand: true,
      modifyExpand: true,
      delExpand: true,
      startComparing: 0,
      result: [],
      insert: [],
      modify: [],
      del: [],
      insertSelect: [],
      modifySelect: [],
      delSelect: [],
      keys: [],
      dest: [], // comparing system, from which user logged in
      src: [], //compared with system, from which data will, be inserted
      isProcessed: 0
    };
  },
  watch: {},
  methods: {
    insertData(arr) {
      let self = this;
      let ins = function() {
        let payload = [];
        if (arr === undefined) {
          payload = self.insert;
        } else {
          arr.forEach(x => {
            payload.push(self.insert[x]);
          });
        }

        https
          .post(
            `/api/configuration/compare.xsjs?table=${self.table}`,
            JSON.stringify(payload)
          )
          .then(
            resolve => {
              EventBus.$emit("Popup", {
                popupType: "Information",
                title: "Insert Records",
                message: resolve.data.status + " : " + resolve.data.message,
                button1: false
              });

              if (arr === undefined) {
                self.insert = [];
              } else {
                arr.forEach(x => {
                  self.insert.splice(x, 1);
                });
              }
              self.insertSelect = [];
            },
            error =>
              EventBus.$emit("Popup", {
                popupType: "Error",
                title: "Insert Records",
                message: error
              })
          );
      };
      let length = arr === undefined ? this.insert.length : arr.length;
      if (length === 0)
        EventBus.$emit("Popup", {
          popupType: "Error",
          title: "Insert Records",
          message: `No records selected..!`
        });
      else
        EventBus.$emit("Popup", {
          popupType: "Confirmation",
          title: "Insert Records",
          message: `Actions are Irreversable. Are you sure you want to insert these ${length} records ?`,
          confirmFunction: ins
        });
    },

    updateData(arr) {
      let self = this;
      let modify = function() {
        let payload = { keys: self.keys, data: [] };
        if (arr === undefined) {
          payload.data = self.modify;
        } else {
          arr.forEach(x => {
            payload.data.push(self.modify[x]);
          });
        }

        https
          .put(
            `/api/configuration/compare.xsjs?table=${self.table}`,
            JSON.stringify(payload)
          )
          .then(
            resolve => {
              EventBus.$emit("Popup", {
                popupType: "Information",
                title: "Update Records",
                message: resolve.data.status + " : " + resolve.data.message,
                button1: false
              });

              if (arr === undefined) {
                self.modify = [];
              } else {
                arr.forEach(x => {
                  self.modify.splice(x, 1);
                });
              }
              self.modifySelect = [];
            },
            error =>
              EventBus.$emit("Popup", {
                popupType: "Error",
                title: "Update Records",
                message: error
              })
          );
      };
      let length = arr === undefined ? this.modify.length : arr.length;
      if (length === 0)
        EventBus.$emit("Popup", {
          popupType: "Error",
          title: "Update Records",
          message: `No records selected..!`
        });
      else
        EventBus.$emit("Popup", {
          popupType: "Confirmation",
          title: "Update Records",
          message: `Actions are Irreversable. Are you sure you want to update these ${length} records ?`,
          confirmFunction: modify
        });
    },

    delData(arr) {
      let self = this;
      let del = function() {
        let payload = [];
        if (arr === undefined) {
          payload = self.del;
        } else {
          arr.forEach(x => {
            let obj = {};
            self.keys.forEach(idx => (obj[idx] = self.del[x][idx]));
            payload.push(obj);
          });
        }

        https
          .delete(`/api/configuration/compare.xsjs?table=${self.table}`, {
            data: JSON.stringify(payload)
          })
          .then(
            resolve => {
              EventBus.$emit("Popup", {
                popupType: "Information",
                title: "Delete Records",
                message: resolve.data.status + " : " + resolve.data.message,
                button1: false
              });

              if (arr === undefined) {
                self.del = [];
              } else {
                arr.forEach(x => {
                  self.del.splice(x, 1);
                });
              }
              self.delSelect = [];
            },
            error =>
              EventBus.$emit("Popup", {
                popupType: "Error",
                title: "Delete Records",
                message: error
              })
          );
      };
      let length = arr === undefined ? this.del.length : arr.length;
      if (length === 0)
        EventBus.$emit("Popup", {
          popupType: "Error",
          title: "Delete Records",
          message: `No records selected..!`
        });
      else
        EventBus.$emit("Popup", {
          popupType: "Confirmation",
          title: "Delete Records",
          message: `Actions are Irreversable. Are you sure you want to delete these ${length} records ?`,
          confirmFunction: del
        });
    },

    getDestData() {
      https
        .get(`/api/configuration/compare.xsjs?table=${this.table}`)
        .then(resolve => {
          this.dest = resolve.data.data;
        });
    },
    getSrcData() {
      https
        .get(
          `${
            this.$store.getters.getSysAddr(this.srcEnv).host
          }/api/configuration/compare.xsjs?table=${this.table}`
        )
        .then(resolve => {
          this.keys = resolve.data.keys;
          this.src = resolve.data.data;
        });
    },
    async process(src, dest, keys) {
      this.startComparing = 1;
      this.isProcessed = 0;
      let sourceHeader = Object.keys(src[0]);
      //let descHeaders = Object.keys(dest);
      let res = -1;

      //Check for Extra Records
      dest.forEach(drow => {
        for (let i = 0; i < src.length; i++) {
          res = this.compare(drow, this.src[i], sourceHeader, keys);
          if (res === 0 || res === 1) break;
        }
        if (res === -1) this.del.push(drow);
      });

      //Check for Insert and Update Actions
      await src.forEach(srow => {
        for (let i = 0; i < dest.length; i++) {
          res = this.compare(srow, this.dest[i], sourceHeader, keys);
          if (res === 0 || res === 1) break;
        }
        if (res === 1) this.modify.push(srow);
        else if (res === -1) this.insert.push(srow);
      });

      this.isProcessed = 1;
    },
    compare(obj1, obj2, columns, keys) {
      let key = [];
      let res = -1;
      let col = [];

      //Check if key columns exist and push the results to key
      keys.forEach(x => {
        key.push(obj1[x] === obj2[x]);
      });

      //ok check if all columns are equal and push the results to col
      if (
        key.every(x => {
          return x;
        })
      ) {
        columns.forEach(y => col.push(obj1[y] === obj2[y]));
      }

      //check if all key column match and all column match then its a complete match => No Action Required
      if (
        key.every(x => {
          return x;
        }) &&
        col.every(x => {
          return x;
        })
      )
        res = 0;
      else if (
        key.every(x => {
          return x;
        })
      )
        //Record Exist but some fields have diff values => Action: Update
        res = 1;
      else res = -1; //Record doesn;t exist, Action => Insert
      return res;
    }
  },
  async created() {
    this.getDestData();
    this.getSrcData();
    //await this.process(this.src, this.dest, this.keys);
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@import "https://fonts.googleapis.com/css?family=Work+Sans|IBM+Plex+Sans|Signika|Bree+Serif|Roboto|Pacifico";

#compare {
  display: flex;
  flex-direction: column;
  margin-left: 20px;
  margin-right: 10px;
  margin-bottom: 30px;
}

#compare > #info {
  border: 1px solid;
  margin-top: 10px;
  padding-top: 4px;
  padding-bottom: 4px;
  padding-left: 5px;
  display: flex;
  flex-direction: row;
  border-radius: 30px;
}

#compare > #info > td:last-child {
  border-right: 0px;
  display: flex;
  justify-content: center;
}
#compare > #info > td:last-child > button {
  height: 30px;
  border-radius: 10px;
  width: 100px;
  font-size: 1em;
  font-weight: bold;
  font-family: "Work Sans", "IBM Plex Sans", sans-serif, Verdana;
  outline: none;
  background: #0dff91;
  border: 0px;
  padding-bottom: 2px;
}

#compare > #info > td:last-child > button:hover {
  background-color: #06ff00;
}
#compare > #info > td:last-child > button:active {
  background-color: #0dff91;
  border: 1px solid #0ce846;
}

#compare > #info > td:last-child > button:disabled {
  background-color: #bbbbbb;
}

#compare > #info > td {
  flex: 1;
  border-right: 1px solid;
  margin-left: 10px;
}

#compare > #info > td > label:first-child {
  font-size: 0.8em;
  font-family: "Work Sans", "IBM Plex Sans", sans-serif, Verdana;
}

#compare > #info > td > label:last-child {
  font-size: 1em;
  font-family: "Work Sans", "IBM Plex Sans", sans-serif, Verdana;
  font-weight: bold;
  margin-left: 5px;
}

#compare > .headers {
  font-size: 1.2em;
  font-family: "Work Sans", "IBM Plex Sans", sans-serif, Verdana;
  border-bottom: 2px solid #868683;
  padding-bottom: 4px;
  padding-left: 20px;
  color: #585753;
  margin-right: 10px;
  margin-bottom: 5px;
  margin-top: 20px;
  font-weight: bold;
}

#compare > .headers > label:nth-child(2) {
  font-size: 0.5em;
  margin-left: 5px;
  font-weight: normal;
}

#compare > .headers > label:nth-child(2)::after {
  content: ")";
}
#compare > .headers > label:nth-child(2)::before {
  content: "(";
}

#compare > .headers > button {
  float: right;
  width: 150px;
  margin-right: 5px;
  border-radius: 30px;
  outline: none;
  font-family: "Work Sans", "IBM Plex Sans", sans-serif, Verdana;
  padding-top: 4px;
  padding-bottom: 4px;
  padding-left: 8px;
  padding-right: 8px;
}

#compare > .headers > img {
  float: right;
  width: 25px;
  height: 25px;
  margin-right: 10px;
  margin-left: 10px;
  border: 1px solid transparent;
  border-radius: 5px;
}

#compare > .headers > img:hover {
  border: 1px solid blue;
  border-radius: 5px;
}

#compare > span {
  display: inline-flex;
  flex-direction: row;
}

#compare > span:nth-child(odd) > div {
  background-color: #e8ecee;
}

#compare > span > div > input {
  width: 20px;
  height: 16px;
  margin-top: 10px;
}

#compare > span > div {
  display: inline-flex;
  border: 1px solid;
  flex-basis: 100%;
  border: 1px solid;
  margin-left: 10px;
  margin-right: 5px;
  padding-left: 5px;
  border-radius: 5px;
  margin-top: 4px;
}

#compare > span > div > .row {
  display: flex;
  flex-direction: column;
  flex: 1;
  margin-left: 5px;
  border-right: 1px solid;
  flex-basis: 0 0;
  margin-top: 2px;
  margin-bottom: 2px;
  padding-right: 5px;
}
#compare > span > div > .row:last-child {
  border-right: 0px;
}

#compare > span > div > .row > .rowitemkey {
  font-size: 0.7em;
  font-family: "IBM Plex Sans", sans-serif, Verdana;
}

#compare > span > div > .row > .rowitemvalue {
  font-size: 0.9em;
  font-family: "Roboto", "IBM Plex Sans", sans-serif, Verdana;
  padding-right: 8px;
  text-overflow: ellipsis;
}

#compare #Notice {
  padding-left: 40%;
  font-family: "Roboto", "IBM Plex Sans", sans-serif, Verdana;
  font-size: 1.2em;
}
#compare #Notice label {
  margin-top: 14px;
  margin-left: 10px;
  font-weight: bold;
}
</style>
