<template>
  <div id="clientmanagement">
    <div id="cmheader">Client Technical User Management</div>
    <div id="cid" class="cmsection">
      <label>Client ID</label>::
      <input
        @keypress="isNumber(event), editcomment = editcomment ? true : editid ? true : false"
        v-model="userdata.clientId"
        :readonly="!editid"
        ref="clientid"
      >
      <button
        style="font-size:1em"
        @click="editid = true, $refs.clientid.focus()"
        v-show="!editid && userdata.clientId"
      ></button>
    </div>
    <div id="cname" class="cmsection">
      <label>Client Name</label>::
      <input
        v-model="userdata.clientName"
        :readonly="!editname"
        ref="clientname"
        @keypress="editcomment = editcomment ? true : editname ? true : false"
      >
      <button
        style="font-size:1em"
        @click="editname = true, $refs.clientname.focus()"
        v-show="!editname"
      ></button>
    </div>

    <div id="cappuser" class="cmsection">
      <label>Application User</label>::
      <input
        placeholder="Technical Application User Name"
        v-model="userdata.appUserName"
        @input="userdata.appUserName = userdata.appUserName.toUpperCase()"
        :readonly="!editappuser"
      >
      <button
        style="font-size:0.6em"
        v-show="userdata.appUserName && currentClientId !== 'Generic' "
        @click="resetPassword(userdata.appUserName)"
      >Reset Password</button>
    </div>
    <div id="ctabuser" class="cmsection">
      <label>Tableau User</label>::
      <input
        placeholder="Technical Tableau User Name"
        v-model="userdata.tableauUserName"
        @input="userdata.tableauUserName = userdata.tableauUserName.toUpperCase()"
        :readonly="!edittablueauuser"
      >
      <button
        style="font-size:0.6em"
        v-show="userdata.tableauUserName && currentClientId !== 'Generic'"
        @click="resetPassword(userdata.tableauUserName)"
      >Reset Password</button>
    </div>
    <div id="cdesc" class="cmsection">
      <label>Comments</label>::
      <input
        v-model="userdata.comments"
        :readonly="!editcomment"
        placeholder="Enter comments for this change"
      >
    </div>
    <div id="cbutton" class="cmsection">
      <button
        :disabled="!editcomment || userdata.appUserName === ''"
        @click="currentClientId === 'Generic' ? createClient() : updateClient()"
      >{{currentClientId !== 'Generic' ? 'Save' : 'Create'}}</button>
      <button :disabled="!editcomment || userdata.appUserName === ''" @click="discard()">Discard</button>
      <button
        v-show="currentClientId !== 'Generic' && userdata.appUserName"
        @click="deleteclient()"
      >Delete User</button>
    </div>

    <fieldset id="generalinfo" v-show="currentClientId !== 'Generic'">
      <legend>Access Info</legend>
      <label>Modified at</label>::
      <input
        disabled
        readonly
        style="border: 0px; outline:none; background:transparent"
        :value="userdata.modifiedAt"
      >
      <label>Modified by</label>::
      <input
        disabled
        readonly
        style="border: 0px; outline:none; background:transparent"
        :value="userdata.modifiedBy"
      >
      <label>Status</label>::
      <input
        disabled
        readonly
        style="border: 0px; outline:none; background:transparent"
        :value="userdata.status === 1 ? 'Active' : 'Inactive'"
      >
    </fieldset>
  </div>
</template>

<script>
import https from "@/https";
import { EventBus } from "@/event-bus.js";

export default {
  name: "clientmanagement",
  components: {},
  data() {
    return {
      authinprocess: false,
      editid: false,
      editname: false,
      editappuser: false,
      edittablueauuser: false,
      editcomment: false,
      currentClientId: null,
      oldcomment: "",
      oldclientid: "",
      newPassword: "",
      userdata: {
        appUserName: "",
        clientName: "",
        clientId: "",
        modifiedAt: "",
        modifiedBy: "",
        comments: "",
        status: "",
        tableauUserName: ""
      }
    };
  },

  watch: {},
  methods: {
    _deleteClient() {
      EventBus.$emit("Popup", {
        popupType: "Loader"
      });

      https
        .delete("/api/configuration/usermgmt.xsjs?user=" + this.currentClientId)
        .then(
          resolve => {
            EventBus.$emit("Popup", {
              popupType: "Information",
              title: "Client Removed..!",
              button1: false,
              message: resolve.data.status
            });
            EventBus.$emit("RemoveClient", {
              id: this.currentClientId,
              name: this.userdata.clientName
            });
          },
          error => {
            EventBus.$emit("Popup", {
              popupType: "Error",
              title: "Could not remove Client",
              message: error
            });
          }
        );
    },
    deleteclient() {
      EventBus.$emit("Popup", {
        popupType: "Confirmation",
        title: "Client Deletion..!",
        message:
          "This is Nonrevokable Action. Are you sure you want to delete this client access?",
        confirmFunction: this._deleteClient
      });
    },
    isNumber: function(evt) {
      evt = evt ? evt : window.event;
      var charCode = evt.which ? evt.which : evt.keyCode;
      if (
        charCode > 31 &&
        (charCode < 48 || charCode > 57) &&
        charCode !== 46
      ) {
        evt.preventDefault();
      } else {
        return true;
      }
    },
    async discard() {
      await this.loadData(this.currentClientId);
      this.editid = false;
      this.editname = false;
      this.editcomment = false;
    },
    createClient() {
      if (this.userdata.appUserName.trim() === "") {
        EventBus.$emit("Popup", {
          popupType: "Error",
          title: "Pre-requisite Failure",
          message: "Application User Cannot be empty."
        });
      } else if (this.userdata.tableauUserName.trim() === "") {
        EventBus.$emit("Popup", {
          popupType: "Error",
          title: "Pre-requisite Failure",
          message: "Tableau User Cannot be empty."
        });
      } else if (
        this.userdata.tableauUserName.trim() ===
        this.userdata.appUserName.trim()
      ) {
        EventBus.$emit("Popup", {
          popupType: "Error",
          title: "Pre-requisite Failure",
          message: "Tableau User cannot be same as App User."
        });
      } else {
        https
          .post(
            "/api/configuration/usermgmt.xsjs?type=check",
            JSON.stringify({
              clientId: parseInt(this.userdata.clientId, 10),
              clientName: this.userdata.clientName,
              appUser: this.userdata.appUserName,
              tableauUser: this.userdata.tableauUserName,
              comments: this.userdata.comments
            })
          )
          .then(
            resolve => {
              let msg = [];
              if (resolve.data.clientId > 0) msg.push("Client ID");
              if (resolve.data.appuser > 0) msg.push("App UserName");
              if (resolve.data.tableauUser > 0) msg.push("Tableau UserName");
              if (msg.length > 0) {
                EventBus.$emit("Popup", {
                  popupType: "Error",
                  title: "Data Already Exist",
                  message:
                    msg.join(" and ") +
                    " already exist in system, Edit and try again"
                });
              } else {
                EventBus.$emit("Popup", { popupType: "Loader" });

                https
                  .post(
                    "/api/configuration/usermgmt.xsjs?type=create",
                    JSON.stringify({
                      clientId: parseInt(this.userdata.clientId, 10),
                      clientName: this.userdata.clientName,
                      appUser: this.userdata.appUserName
                        .trim()
                        .replace(" ", "_"),
                      tableauUser: this.userdata.tableauUserName
                        .trim()
                        .replace(" ", "_"),
                      comments: this.userdata.comments
                    })
                  )
                  .then(
                    resolve => {
                      this.newPassword = resolve.data.password;
                      EventBus.$emit("Popup", {
                        popupType: "Information",
                        title: "Client Created",
                        message: `Client Created Successfully...!  Password => ${
                          resolve.data.password
                        }`,
                        button1Name: "Copy to Clipboard",
                        confirmFunction: this.copyToClipboard
                      });
                      EventBus.$emit("AddClient", {
                        id: this.userdata.appUserName,
                        name: this.userdata.clientName,
                        clientid: parseInt(this.userdata.clientId, 10)
                      });
                      this.loadGenericData();
                    },
                    error => {
                      EventBus.$emit("Popup", {
                        popupType: "Error",
                        title: "Client Creation Failed",
                        message: error
                      });
                    }
                  );
              }
            },
            error => {
              EventBus.$emit("Popup", {
                popupType: "Error",
                title: "Precheck Failed..!",
                message: error
              });
            }
          );
      }
    },
    updateClient() {
      if (
        this.oldcomment === this.userdata.comments ||
        this.userdata.comments.trim() === ""
      ) {
        EventBus.$emit("Popup", {
          popupType: "Error",
          title: "Prerequisite Failed",
          message: "Comments should be updated for Client Data Update"
        });
      } else if (this.userdata.clientId === "") {
        EventBus.$emit("Popup", {
          popupType: "Error",
          title: "Prerequisite Failed",
          message: "Client Id should not be empty."
        });
      } else {
        https
          .post(
            "/api/configuration/usermgmt.xsjs?type=update",
            JSON.stringify({
              clientId: parseInt(this.userdata.clientId, 10),
              clientName: this.userdata.clientName,
              appUser: this.userdata.appUserName,
              tableauUser: this.userdata.tableauUserName,
              comments: this.userdata.comments
            })
          )
          .then(
            resolve => {
              if (resolve.data === 1) {
                EventBus.$emit("Popup", {
                  popupType: "Information",
                  title: "Updated Successfully...!",
                  message: "Client data updated successfully...!",
                  button1: false
                });
                this.loadData(this.currentClientId);
                EventBus.$emit("UpdateClient", {
                  id: this.userdata.appUserName,
                  name: this.userdata.clientName
                });
              }
            },
            error => {
              EventBus.$emit("Popup", {
                popupType: "Error",
                title: "Error while Update",
                message: error
              });
            }
          );
      }
    },
    copyToClipboard() {
      const el = document.createElement("textarea");
      el.value = this.newPassword;
      document.body.appendChild(el);
      el.select();
      document.execCommand("copy");
      document.body.removeChild(el);
      EventBus.$emit("Popup", {
        popupType: "Information",
        title: "Copy to Clipboard",
        message: "Password copied to clipboard",
        button1: false
      });
    },
    resetPassword(id) {
      https
        .post(
          "/api/configuration/usermgmt.xsjs?type=reset",
          JSON.stringify({
            userName: id,
            comment: this.userdata.comments + " / Password Reseted"
          })
        )
        .then(
          resolve => {
            this.newPassword = resolve.data.password;
            EventBus.$emit("Popup", {
              popupType: "Information",
              title: "Password Reset.",
              message: resolve.data.password,
              button1Name: "Copy to Clipboard",
              confirmFunction: this.copyToClipboard
            });
          },
          error => {
            EventBus.$emit("Popup", {
              popupType: "Error",
              title: "Error in reset password",
              message: error
            });
          }
        );
    },
    loadData(client) {
      https.get("/api/configuration/usermgmt.xsjs?user=" + client).then(
        resolve => {
          this.userdata = resolve.data;
          this.oldcomment = resolve.data.comments;
          this.oldclientid = resolve.data.clientId;
        },
        error => {
          EventBus.$emit("Popup", {
            popupType: "Error",
            title: "Error",
            message: error
          });
        }
      );
    },
    loadGenericData() {
      this.editid = true;
      this.editname = true;
      this.editappuser = true;
      this.edittablueauuser = true;
      this.editcomment = true;
      this.userdata = {
        appUserName: "",
        clientName: "",
        clientId: "",
        modifiedAt: "",
        modifiedBy: "",
        comments: "",
        status: 0,
        tableauUserName: ""
      };

      https.get("/api/configuration/usermgmt.xsjs?user=create").then(
        resolve => (this.userdata.clientId = resolve.data.clientid),
        error => {
          this.userdata.clientId = "";
          EventBus.$emit("Popup", {
            popupType: "Error",
            title: "Un-Authorized",
            message: error
          });
        }
      );
    }
  },
  created() {
    this.currentClientId = this.$route.query.client;
    if (this.currentClientId === "Generic") {
      this.loadGenericData();
    } else {
      this.loadData(this.$route.query.client);
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@import "https://fonts.googleapis.com/css?family=Work+Sans|IBM+Plex+Sans|Signika|Bree+Serif|Roboto|Pacifico";

#clientmanagement {
  display: flex;
  flex-direction: column;
  margin-left: 30px;
  margin-top: 60px;
}

#clientmanagement #cmheader {
  border-bottom: 2px solid;
  display: block;
  width: 90%;
  padding-left: 20px;
  font-family: "Pacifico", "Signika", "IBM Plex Sans", sans-serif, Verdana;
  font-size: 1.6em;
  letter-spacing: 1px;
  color: #3d4246;
  padding-bottom: 6px;
  margin-bottom: 80px;
}

#clientmanagement .cmsection {
  margin-left: 20px;
  font-family: "IBM Plex Sans", sans-serif, Verdana;
  font-size: 1.4em;
  font-weight: bold;
  margin-top: 20px;
}

#clientmanagement .cmsection label {
  display: inline-block;
  width: 200px;
}

#clientmanagement .cmsection input {
  outline: none;
  border: 0px;
  border-bottom: 2px solid;
  margin-left: 12px;
  padding-left: 10px;
  font-family: "IBM Plex Sans", sans-serif, Verdana;
  font-size: 1.4em;
  padding-right: 10px;
}

#clientmanagement .cmsection input:focus {
  border-color: #116eff;
}

#clientmanagement .cmsection input:read-only:focus {
  border-color: #606e7f;
}

#clientmanagement #cid input {
  width: 140px;
  text-align: center;
}

#clientmanagement #cname input {
  width: 260px;
}

#clientmanagement #cappuser input,
#clientmanagement #ctabuser input {
  width: 480px;
  font-size: 1.2em;
  text-overflow: ellipsis;
}

#clientmanagement #cdesc input {
  width: 700px;
  font-size: 1em;
}

#clientmanagement #cbutton {
  margin-top: 60px;
  margin-right: 80px;
}

#clientmanagement .cmsection button {
  border-radius: 2px;
  outline: none;
  border: 0px;
  margin-left: 10px;
  text-align: center;
}

#clientmanagement .cmsection button:hover {
  color: greenyellow;
  background-color: #3d4246;
}

#clientmanagement .cmsection button:active {
  color: #3d4246;
  background-color: #cfcfcf;
}

#clientmanagement #cbutton button {
  outline: none;
  border: 0px;
  margin-left: 10px;
  padding-left: 10px;
  font-family: "Roboto", sans-serif, Verdana;
  font-size: 1.1em;
  padding-right: 10px;
  border-radius: 4px;
  float: right;
  height: 40px;
}

#clientmanagement #cbutton button:nth-child(1):hover {
  background-color: #0ce846;
  color: white;
}

#clientmanagement #cbutton button:nth-child(2):hover {
  background-color: #3d4246;
  color: white;
}

#clientmanagement #cbutton button:nth-child(3):hover {
  background-color: #ff9f0d;
  color: white;
}

#clientmanagement #cbutton button:nth-child(1):active,
#clientmanagement #cbutton button:nth-child(2):active,
#clientmanagement #cbutton button:nth-child(3):active {
  color: black;
  background-color: #cfcfcf;
}

#clientmanagement #cbutton button:nth-child(1):disabled,
#clientmanagement #cbutton button:nth-child(2):disabled {
  background-color: #cfcfcf;
  color: #a7a1a1;
}

#clientmanagement #cbutton button:nth-child(3) {
  margin-right: 50px;
}

#clientmanagement #generalinfo {
  min-width: 300px;
  max-width: 300px;
  margin-top: 30px;
  font-family: "Roboto", sans-serif, Verdana;
  border-radius: 5px;
  margin-left: 25px;
}

#clientmanagement #generalinfo legend {
  font-family: "Work Sans", sans-serif, Verdana;
  font-weight: bold;
}

#clientmanagement #generalinfo label {
  display: inline-block;
  min-width: 75px;
  font-size: 0.8em;
  font-family: "IBM Plex Sans", sans-serif, Verdana;
}

#clientmanagement #generalinfo input {
  display: inline-block;
  min-width: 200px;
  font-size: 0.9em;
  font-family: "IBM Plex Sans", sans-serif, Verdana;
  margin-left: 10px;
}
</style>
