import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    logedin: false, 
    logedinenv: "",
    hosts: [
      {
        id: "DEV",
        host: "https://rsdxre3qgxzre.eu2.hana.ondemand.com/mr/mind/master/common"
      },
      {
        id: "INT",
        host: "https://rsilk2l4ybq0o.eu2.hana.ondemand.com/mr/mind/master/common"
      },
      {
        id: "PRD",
        host: "https://rspe137137c0.eu2.hana.ondemand.com/mr/mind/master/common"
      }
    ]
  },
  getters: {
    isLogedin: state => {
      return state.logedin;
    },
    getSysAddr: state => sys => {
      return state.hosts.find(env => env.id === sys);
    },
    getCurrentSys: state => {
      return state.logedinenv;
    }
  },
  mutations: {
    isAuthenticated(state, logedin) {
      state.logedin = logedin;
    },
    authenticatedEnv(state, env) {
      state.logedinenv = env;
    }
  },
  actions: {
    isAuthenticated(context, logedin) {
      return new Promise(resolve => {
        context.commit("isAuthenticated", logedin);
        resolve(this.getters.isLogedin);
      });
    },
    authenticatedEnv(context, env) {
      return new Promise(resolve => {
        context.commit("authenticatedEnv", env);
        resolve(this.getters.getSysAddr(env));
      });
    }
  }
});
